.total{
    background: radial-gradient(50% 50% at 50% 50%, rgba(255, 255, 255, 0) 0%, #FFAA01 100%), #FFFFFF;
    padding-bottom: 54px;
    padding-top: 33px;
}

@media (min-width:992px){
    .total{
        padding-top: 63px;
    }
}

.foto{
    background-image: url('../../../image/Rectangle\ 10.png');
    background-repeat: round;
    
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 145px;
    width: 145px;
    position: relative;
    margin-top: 34px;
    margin-bottom: 34px;
}
@media (min-width:992px){
    .doacoes-alinhamento-foto{
align-items: center;
    }
}

@media (min-width:992px){
    .foto{
        
    width: 350px;
    height: 350px;
    background-repeat:inherit ;
    
    }
}
.foto2{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@media (min-width:992px){
    .foto2{
     width: 243.48px;
     height: 237.39px;

